.profile {
  &__wrapper {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    width: 50vw;
    margin: 0 auto;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    &__item-data {
      color: #999;
    }
  }

  &__sidebar {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 5vw;
    height: 100vh;
    background-color: #fbfbfb;
  }

  &__btn {
    width: 28px;
    height: 28px;
    background-image: url("data:image/svg+xml,%3Csvg width='28' height='28' viewBox='0 0 28 28' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Ccircle cx='14' cy='14' r='14' transform='rotate(-180 14 14)' fill='%233369F3'/%3E%3Crect x='20' y='14.8' width='11' height='1.6' transform='rotate(-180 20 14.8)' fill='white'/%3E%3Cpath d='M13 19L9 14L13 9' stroke='white' stroke-width='1.6'/%3E%3C/svg%3E");
    background-position: center;
    background-repeat: no-repeat;
    border: none;
  }

  &__item {
    display: flex;
    justify-content: space-between;
    width: 100%;
    border-bottom: 1px solid #eaeaea;
    padding-bottom: 10px;
    margin-bottom: 10px;

    &:last-child {
      border: none;
    }

    input {
      border: none;
      text-align: right;
    }

    input:focus {
      outline: 1px solid $primary-color;
    }

    input:read-only {
      outline: none;
    }
  }

  &__header {
    text-align: center;
    margin-bottom: 60px;

    #avatar {

    }
  }

  &__avatar {
    border-radius: 50%;
    margin-bottom: 21px;

    cursor: pointer;
    transition: scale .5s;

    &:hover {
      transform: scale(1.1);
      box-shadow: rgba(0, 0, 0, 0.24) 0 3px 8px;
    }
  }

  &__content {
    width: 100%;
    margin-bottom: 60px;
  }

  &__footer {
    width: 100%;
    display: flex;
    flex-direction: column;

    &-item {
      width: 25%;
      border: none;
      background: none;
      text-align: left;
      font-size: 16px;
      color: $primary-color;
      cursor: pointer;
      margin-bottom: 20px;

      &--logout {
        color: #f00;
      }
    }
  }
}

.avatar-form {
  display: flex;
  flex-direction: column;
}
